<template >

  <div class="vx-col w-full mb-base" v-if="renderComponent">

    <vx-card>
       <vs-tabs>
         <vs-tab label="File Data">
            <div class="tab-text">
              <vs-table pagination max-items="10" search :data="files">

                    <template slot="thead">
                      <vs-th sort-key="username">{{$t('username')}}</vs-th>
                      <vs-th sort-key="file_name">{{$t('file_name')}}</vs-th>
                      <vs-th sort-key="upload_date">{{$t('upload_date')}}</vs-th>


                      <vs-th class="text-center">{{$t("download")}}</vs-th>
                      <vs-th>{{$t("delete")}}</vs-th>
                    </template>

                    <template slot-scope="{data}">
                      <vs-tr :data="tr" :key="index" v-for="(tr, index) in data">

                        <vs-td :data="tr.username">
                          {{tr.username}}
                        </vs-td>

                        <vs-td :data="tr.file_name">
                          {{ tr.file_name }}
                        </vs-td>

                        <vs-td :data="tr.upload_date">
                          {{ tr.upload_date }}
                        </vs-td>


                        <vs-td class="text-center">
                          <feather-icon icon="DownloadIcon" class="cursor-pointer w-6 h-6" size="small" @click="download(tr)"></feather-icon>
                        </vs-td>
                        <vs-td>
                          <feather-icon icon="Trash2Icon" class="cursor-pointer w-6 h-6" size="small" @click="delete_file(tr)"></feather-icon>
                        </vs-td>
                      </vs-tr>
                    </template>
                  </vs-table>
            </div>
          </vs-tab>
          <vs-tab label="Upload File">
            <div class="tab-text">
               <div class="vx-row">
                  <div class="vx-col md:w-1/3">
                    <label for="">choose file</label>
                    <vs-input type="file" @change="handleFileUpload($event)"/>
                  </div>
                  <div class="vx-col md:w-1/3">
                    <div class="mt-6">
                      <vs-button color="rgb(62, 201, 214)" type="filled" @click="upload()">upload</vs-button>
                    </div>
                  </div>
                </div>
            </div>
          </vs-tab>

    </vs-tabs>

    </vx-card>

  </div>
  <!-- <button>click</button> -->
</template>
<style lang="scss" scoped>

  .require{
    color: red;
  }

  .danger input {
    padding: .7rem;
    font-size: 1rem;
    border-radius: 5px;
    border: 1px solid red;
    color: #626262;
    width: 100%;
  }
   .error{
    color:red;
     font-size:80%;
  }
  .text-center .vs-table-text{
    text-align: center;
  }
</style>


<script>
import Datepicker from 'vuejs-datepicker';
import service from '@/service/service';

import moment from 'moment';
// import firebase from 'firebase/app'à¸§
// import * as firebase from 'firebase/app';

// import '@firebase/messaging';
export default {
  components:{
    Datepicker
  },
   data(){
      return {
         'tableList': [
        'vs-th: Component',
        'vs-tr: Component',
        'vs-td: Component',
        'thread: Slot',
        'tbody: Slot',
        'header: Slot'
      ],
      url:`${service.url}/upload_file?username=${service.getUser().username}`,
      popupActive:false,
      renderComponent:true,
      files:[],
      file:""
      }
   },

   computed:{
      validateForm() {

        }
   },
   created(){

    this.get_file();
   },
   methods: {
     get_file(){
        service.postData("get_file",{username:service.getUser().username}).then((result)=>{
       this.files=result.map((item)=>{
         item.upload_date=moment(item.upload_date).format('DD/MM/YYYY HH:mm:ss');
         return item;
       });
     })


     },
     forceRerender(){
       this.renderComponent=false;
       this.$nextTick(()=>{
         this.renderComponent=true;
         this.get_file();
       })
     },
     handleFileUpload(e){
        this.file = e.target.files[0];

      },
      upload(){
       if(this.file!=""){
          // console.log(e);
          let formData = new FormData();
          formData.append('file', this.file);
          service.upload("upload_file?username="+service.getUser().username,formData).then((result)=>{
            this.$swal('file was uploaded','','success').then((result)=>{
              this.forceRerender();
            });
          })
       }
      },
      successUpload(){
      this.$vs.notify({color:'success',title:'Upload Success',text:'Lorem ipsum dolor sit amet, consectetur'})
    },
    download(file){
      var win = window.open("http://localhost:3000/files/"+file.file_name, '_blank');
      win.focus();
    },
    delete_file(file){
      this.$swal({
          title: 'Are you sure to delete this file?',
          text: "You won't be able to revert this!",
          type: 'warning',
          showCancelButton: true,
          confirmButtonColor: '#3085d6',
          cancelButtonColor: '#d33',
          confirmButtonText: 'delete'
        }).then((result) => {
          console.log(result);
          if (result.value) {
            service.postData("delete_file",{file_id:file.file_id}).then((result)=>{
                this.$swal('file was deleted','','success').then((result)=>{
                  this.forceRerender();
                });
              })
          }
      })

    }
   },
  watch: {

    }
}
</script>

